.Loading {
  display: flex;
  margin-top: 6em;

  .Loading-logo {
    animation: Loading-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
    color: #fff;
  }

  @keyframes Loading-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

}
